// Styles for buttons:
.button-menu{
    position: fixed;
    top: 0px;
    left: 0px;
    background-color: $color-primary-600;
    width: 44px;
    height: 44px;
}


// Styles for txt-buttons:
// General rules for text-buttons
.btn{
    margin: 0 auto;
    height: 45px;
}

.btn-icon{
    width: 45px;
    background-color: $color-neutral-0;
    border-radius: 50%;
    @include flex-centered;
}

.btn-txt{
    width: 76px;
    background-color: $color-neutral-0;
    border-radius: 25px;
    border: 2px solid $color-secondary-600;
    @include flex-centered;

    
}



